img {
	display: inline-block;
	max-width: 100%; }
svg {
	display: inline-block;
	max-width: 100%;
	max-height: 100%; }

.main {
	display: block;
	flex: 1 0 auto;
	width: 100%;
	overflow-x: hidden;
	overflow-y: auto;
	padding-top: 85px;
	@include r(1024) {
		padding-top: 50px; } }

.container {
	width: 100%;
	max-width: 1280px;
	padding: 0 20px;
	margin: 0 auto;
	@include r(1024) {
		padding: 0 20px; } }

.wrap-in {
	padding: 80px 0 70px;
	@include r(1024) {
		padding: 40px 0 40px; } }

.devider {
	border: none;
	width: 100%;
	height: 1px;
	padding: 0;
	margin: 0;
	background: $main;
	opacity: 0.1; }

.bg-gray {
	background: rgba($main, .05); }

.text-row {
	$this: &;
	display: flex;
	flex-flow: row wrap;
	// padding: 80px 0
	&--center {
		text-align: center;
		max-width: 700px;
		margin: 0 auto;
		#{$this}__title {
			width: 100%;
			padding: 0;
			margin-bottom: 30px; }
		#{$this}__text {
			width: 100%;
			padding: 0; } }
	&--border {}
	&--no-top {
		padding-top: 0; }
	@include r(900) {
		padding: 60px 0;
		&--no-top {
			padding-top: 0; } }
	&__title {
		width: 100%;
		padding-bottom: 20px;
		@include rm(901) {
			width: 50%;
			padding-right: 30px; } }
	&__text {
		width: 100%;
		@include rm(901) {
			width: 50%;
			padding-left: 30px; } } }
